import { z } from 'zod';
import {
  FilterValueType,
  SemanticTriggerFilterOperatorsEnum,
} from '../type-enums';

export const SemanticTriggerFilterSchema = z.object({
  dimension: z.string().min(1),
  operator: z.nativeEnum(SemanticTriggerFilterOperatorsEnum),
  values: FilterValueType,
});

export interface SemanticTriggerFilterInterface {
  dimension: string;
  operator: SemanticTriggerFilterOperatorsEnum;
  values: FilterValueType;
}
